<template>
  <div>
    <van-tabs
      @change="checkValue"
      title-active-color="#ff6623"
      color="#ff6623"
    >
      <van-tab
        title="未收款账单"
        name="0"
      > </van-tab>
      <van-tab
        title="已收款账单"
        name="1"
      ></van-tab>
    </van-tabs>
    <div
      v-if='listArr.length > 0'
      :key="contentKey"
      class="agreementScroll"
      :style="{ height: actived == 0 ? 'calc(100vh - 150px)' : 'calc(100vh - 110px)' }"
    >
      <div
        v-for="(item, index) in listArr"
        :key="index"
      >
        <div
          class="noPaid-time"
          @click="toggle(item)"
        >
          <div>
            <span class="time-title">{{ item.m }}月账单</span>
            <span
              class="time-price"
              v-show="item.statu == true"
            >总收入&yen;{{ priceAll[item.y + '-' + item.m] || '0' | PriceF }} </span>
          </div>
          <div v-if="item.statu">
            <van-icon name="arrow-down" />
          </div>
          <div v-else>
            <van-icon name="arrow" />
          </div>
        </div>
        <div>
          <ul
            v-show="item.statu"
            :key="key"
          >
            <li
              class="my-li"
              v-for="(item_o, index) in options[item.y + '-' + item.m]"
              :key="index"
            >
              <div class="day-content content-top mr10">
                <div class="day-content">
                  <van-icon
                    @click="roomTurn(item_o)"
                    :name="item_o.isChecked"
                    :style="{ color: item_o.isChecked == 'checked' ? '#ff7d41' : '#9a9b9c' }"
                  />
                  <div
                    class="content-left"
                    :style="{ 'padding-left': actived == 1 ? '12px' : '' }"
                  >
                    <div class="my-color-000">{{ item_o.roomInfo | splitRoomSn }}</div>
                    <div>({{ item_o.rentalBillSn ? '房费' : '生活' }}账单)</div>
                  </div>
                </div>
                <div
                  class="my-van-icon"
                  @click="isShowMonth(item_o)"
                >
                  <span class="my-color-000">&yen;{{ item_o.rentalBillSn ? item_o.roomPrice : item_o.totalPrice | PriceF }}</span>
                  <van-icon :name="myLookMoreStyle" />
                </div>
              </div>
              <div v-show="item_o.isShow">
                <div class="day-content m40">
                  <div>缴费周期</div>
                  <div>{{ item_o.startTime | dataFilter }}至{{ item_o.endTime | dataFilter }}</div>
                </div>
                <div class="day-content m40 content-top">
                  <div>截止日期</div>
                  <div>{{ item_o.rentalBillSn ? item_o.startTime : item_o.endTime | dataFilter }}</div>
                </div>
                <div
                  class="p10"
                  @click="item_o.isShow = false"
                >
                  <span>收起</span>
                  <van-icon name="arrow-up" />
                </div>
              </div>
            </li>
          </ul>
        </div>
      </div>
    </div>
    <div
      class="bottom-allPrice"
      v-if="actived == 0"
    >
      <div
        class="bottom-left"
        @click="isAllChecked"
      >
        <van-icon
          :name="myAllCheckedStyle"
          :style="{ color: myAllCheckedStyle == 'checked' ? '#ff7d41' : '#9a9b9c' }"
        />&ensp;全选
      </div>
      <div class="bottom-center">
        总计<label>
          &yen; <span>{{ payForm.totalPrice | PriceF }}</span></label>
      </div>
      <van-button
        v-if="listArr.length > 0"
        class="bottom-right code_s"
        @click="noPay"
      >收款码</van-button>
      <van-button
        v-if="is_businessPayOffline == true"
        class="bottom-right"
        @click="btnGo"
      >到账</van-button>
    </div>
    <van-popup
      v-model="showPayed"
      round
      bind:close="onClose"
    >
      <h3>到账</h3>
      <van-cell-group>
        <van-form @submit="confirmPayed">
          <van-field
            v-model="payForm.payTime"
            readonly
            label="到账日期"
            placeholder="点击选择到账日期"
            @click.native="timeShow = true"
            :rules="[{ required: true, message: '请选择到账日期' }]"
          />
          <van-field
            v-model="payForm.paymentValue"
            readonly
            label="支付方式"
            placeholder="请选择"
            @click="getPaymentType"
            :rules="[{ required: true, message: '请选择支付方式' }]"
          />
          <van-field
            v-model="payForm.payeeAccountIdValue"
            readonly
            label="收款账户"
            placeholder="请选择收款账户"
            @click="payeeAccountIdValueShow = true"
          />
          <van-field
            v-model="payForm.totalPrice"
            readonly
            label="到账金额"
            placeholder="请输入到账金额"
          />
          <van-field
            v-model="payForm.username"
            label="确认人"
            readonly
          />
          <van-field
            v-model="payForm.remark"
            label="备注"
            placeholder="请输入备注"
          />
          <van-button class="payedColor">确定收款</van-button>
        </van-form>
      </van-cell-group>
    </van-popup>
    <!-- 到账日期 -->
    <van-calendar
      style="width: 100%"
      v-model="timeShow"
      :min-date="minDate"
      :max-date="maxDate"
      @close="timeShow = false"
      @confirm="onConfirmTime"
    />
    <!-- 支付方式 -->
    <van-popup
      style="width: 100%"
      v-model="paymentShow"
      position="bottom"
    >
      <van-picker
        show-toolbar
        :columns="payments"
        @confirm="onConfirmPayment"
        @cancel="paymentShow = false"
      />
    </van-popup>
    <!-- 收款账户 -->
    <van-popup
      style="width: 100%"
      v-model="payeeAccountIdValueShow"
      position="bottom"
    >
      <van-picker
        show-toolbar
        :columns="users"
        @confirm="onConfirm"
        @cancel="payeeAccountIdValueShow = false"
      />
    </van-popup>
  </div>
</template>

<script>
import { GetBusinessAccountUnitUnpay, GetBusinessAccountUnitPayed, GetBusinessAccountUnitPay, GetBusinessAccountUnitPayedMonth, GetBusinessAccountUnitUnpayMonth } from '@/api/api_agreement';
import { GetBusinessHotelPayeeAccountByhotel } from '@/api/api_business';
import { GetSystemDictDataByType } from '@/api/api_query';
import { Toast } from 'mint-ui';
import dayjs from 'dayjs';
import { isPermission } from '@/utils/filters';
export default {
    name: 'index',
    data() {
        return {
            businessList: [],
            key: 0,
            listArr: [],
            item: '',
            statu: false,
            options: {},
            s: '',
            priceAll: {},
            refuseAll: {},
            selected: '1',
            contentKey: 0,
            day: 14,
            timecheckValue: null,
            currentMounth: null,
            outBill: null,
            num: '1',
            showRoomPayed: false,
            showPayed: false,
            value: '0',
            actived: '0',
            topValue: ['未收款账单', '已收款账单'],
            roomStyle: 'circle',
            myAllCheckedStyle: 'circle',
            myLookMoreStyle: 'arrow',
            roomPrice: 0,
            timeShow: false,
            payeeAccountIdValueShow: false,
            paymentShow: false,
            minDate: null,
            maxDate: null,
            users: [],
            payments: [],
            dataForm: {
                billTime: '',
                accountUnitId: ''
            },
            payForm: {
                billSn: '',
                payTime: '',
                paymentValue: '',
                payment: '',
                payeeAccountId: '',
                totalPrice: 0,
                payeeAccountIdValue: '',
                username: null,
                remark: ''
            },
            checkedCount: 0,
            currentList: [],
            is_businessPayOffline: true
        };
    },
    filters: {
        PriceF(val) {
            return Number(val).toFixed(2);
        }
    },
    created() {
        this.is_businessPayOffline = isPermission('business:pay:offline');
        this.minDate = new Date(dayjs().subtract(6, 'month'));
        this.maxDate = new Date(dayjs().add(6, 'month'));
        this.$store.commit('setHeader', `${this.$route.query.cooperationName}`);
        this.dataForm.accountUnitId = this.$route.query.accountUnitId;
        this.payForm.username = JSON.parse(localStorage.getItem('current_user')).realName || JSON.parse(localStorage.getItem('current_user')).nickName;
        this.getUnpayMonth();
    },
    watch: {
        actived(neww, oldd) {
            if (neww == 0) {
                this.getUnpayMonth();
            } else {
                this.getPayedMonth();
            }
        },
        currentList(neww, oldd) {
            if (neww.length > 0) {
                this.payForm.billSn = '';
                this.payForm.totalPrice = 0;
                this.checkedCount = 0;
                this.totleLength = 0;
                let sumValue = 0;
                neww.map((item) => {
                    sumValue += item.lrPrice;
                });
                this.totleLength = neww.length;
                let c_checked = neww.filter((item) => item.isChecked == 'checked');
                let _arr = [];
                if (c_checked.length > 0) {
                    this.checkedCount = c_checked.length;
                    c_checked.map((item) => {
                        _arr.push(item.rentalBillSn || item.lifeCostSn);
                        this.payForm.totalPrice += item.lrPrice;
                    });
                    if (this.payForm.billSn.length > 0) {
                        this.payForm.billSn = this.payForm.billSn.substr(0, this.payForm.billSn.length - 1);
                    }
                }
                this.payForm.billSn = _arr.join(',');
                if (neww.length == this.checkedCount) {
                    this.myAllCheckedStyle = 'checked';
                } else {
                    this.myAllCheckedStyle = 'circle';
                }
            }
        }
    },
    methods: {
        getUnpayMonth() {
            GetBusinessAccountUnitUnpayMonth({ accountUnitId: this.$route.query.accountUnitId }).done((res) => {
                this.listArr = [];
                if (res.data.length == 0) {
                    return;
                } else {
                    res.data.sort(function (a, b) {
                        return Date.parse(b.replace(/-/g, '/')) - Date.parse(a.replace(/-/g, '/'));
                    });
                    res.data.map((item) => {
                        let obj = {};
                        obj.statu = false;
                        obj.y = item.split('-')[0];
                        obj.m = item.split('-')[1];
                        this.listArr.push(obj);
                    });
                    this.toggle(this.listArr[0], 1);
                }
            });
        },
        getPayedMonth() {
            GetBusinessAccountUnitPayedMonth({ accountUnitId: this.$route.query.accountUnitId }).done((res) => {
                this.listArr = [];
                if (res.data.length == 0) {
                    return;
                }
                res.data.sort(function (a, b) {
                    return Date.parse(b.replace(/-/g, '/')) - Date.parse(a.replace(/-/g, '/'));
                });
                res.data.map((item) => {
                    let obj = {};
                    obj.statu = false;
                    obj.y = item.split('-')[0];
                    obj.m = item.split('-')[1];
                    this.listArr.push(obj);
                });
                this.toggle(this.listArr[0], 1);
            });
        },
        checkValue(event) {
            this.payForm.totalPrice = 0;
            this.myAllCheckedStyle = 'circle';
            this.payForm.billSn = '';
            this.actived = event;
            this.priceAll = {};
            this.listArr.forEach((item) => {
                item.statu = false;
            });
            this.options = [];
            this.toggle(this.listArr[0], 1);
        },
        // 切换滑块
        toggle(item, first) {
            this.item = item;
            if (first == 1) {
                item.statu = true;
            } else {
                item.statu = !item.statu;
            }
            if (item.statu == true) {
                this.getprice(item.y, item.m);
            } else {
                delete this.options[item.y + '-' + item.m];
                let optValues = Object.values(this.options);
                this.currentList = this.abc(optValues);
                if (this.currentList.length == 0) {
                    this.payForm.billSn = '';
                    this.myAllCheckedStyle = 'circle';
                    this.payForm.totalPrice = 0;
                }
            }
        },
        // 缴费情况
        getprice(y, m) {
            this.dataForm.billTime = y + '-' + m;
            if (this.actived == 0) {
                GetBusinessAccountUnitUnpay(this.dataForm).done((result) => {
                    this.$set(this.options, [y + '-' + m], []);
                    let _arrMixList = result.data.concat(result.order);
                    for (let i = 0; i < _arrMixList.length; i++) {
                        _arrMixList[i].isChecked = 'circle';
                        _arrMixList[i].isShow = false;
                        _arrMixList[i].lrPrice = 0;
                    }
                    this.options[y + '-' + m] = _arrMixList;
                    this.dealPrices(this.options[y + '-' + m], y + '-' + m);
                    ++this.contentKey;
                    let optValues = Object.values(this.options);
                    this.currentList = this.abc(optValues);
                });
            } else {
                GetBusinessAccountUnitPayed(this.dataForm).done((result) => {
                    this.$set(this.options, [y + '-' + m], []);
                    let _arrMixList = result.data.concat(result.order);
                    for (let i = 0; i < _arrMixList.length; i++) {
                        _arrMixList[i].isShow = false;
                        _arrMixList[i].lrPrice = 0;
                    }
                    this.options[y + '-' + m] = _arrMixList;
                    this.dealPrices(this.options[y + '-' + m], y + '-' + m);
                    ++this.contentKey;
                });
            }
        },
        abc(arr) {
            return [].concat(...arr.map((item) => [].concat(item)));
        },
        dealPrices(options, ym) {
            let priceAll = 0;
            options.map((item) => {
                if (item.roomPrice) {
                    item.lrPrice = item.roomPrice || 0;
                }
                if (item.totalPrice) {
                    item.lrPrice = item.totalPrice || 0;
                }
                priceAll += item.lrPrice;
                this.priceAll[ym] = priceAll;
                ++this.key;
            });
        },
        //路径跳转
        routerGo(link, item) {
            this.$router.push({ path: link, query: { cooperationId: item.id, hotelId: this.$route.query.hotelId, cooperationName: item.cooperationName, contact: item.contact, phone: item.phone } });
        },
        // 收款码
        noPay() {
            if (!this.payForm.billSn) {
                Toast({
                    message: '请选择账单',
                    darution: 900
                });
                return;
            }
            this.$router.push({
                path: '/roomCode-page',
                query: {
                    roomPrice: this.payForm.totalPrice,
                    billSn: this.payForm.billSn
                }
            });
        },
        btnGo() {
            if (!this.payForm.billSn) {
                Toast({
                    message: '请选择账单',
                    darution: 900
                });
                return;
            }
            this.showPayed = true;
            this.getType();
        },
        // 去重
        unique(arr) {
            return Array.from(new Set(arr));
        },
        // 查看更多
        isShowMonth(item) {
            item.isShow = !item.isShow;
        },
        // 选中累加价格
        roomTurn(item) {
            if (item.isChecked == 'circle') {
                item.isChecked = 'checked';
                this.payForm.totalPrice += item.lrPrice;
                this.checkedCount += 1;
            } else {
                item.isChecked = 'circle';
                this.payForm.totalPrice -= item.lrPrice;
                this.checkedCount -= 1;
            }
            if (this.totleLength == this.checkedCount) {
                this.myAllCheckedStyle = 'checked';
            } else {
                this.myAllCheckedStyle = 'circle';
            }

            let _arrList = [];
            for (const k in this.options) {
                _arrList = _arrList.concat(this.options[k]);
            }
            this.payForm.billSn = _arrList
                .map((item) => {
                    return (item.isChecked == 'checked' && item.rentalBillSn) || (item.isChecked == 'checked' && item.lifeCostSn);
                })
                .filter((item) => item)
                .join(',');
        },
        // 全选
        isAllChecked() {
            if (this.myAllCheckedStyle == 'circle') {
                this.myAllCheckedStyle = 'checked';
                let optKeys = Object.keys(this.options);
                let optionLength = 0;
                optKeys.forEach((key) => {
                    this.options[key].forEach((item1) => {
                        optionLength += this.options[key].length;
                    });
                });
                this.checkedCount = optionLength;
            } else {
                this.myAllCheckedStyle = 'circle';
                this.checkedCount = 0;
            }

            let arrlist = [];
            this.listArr.map((item) => {
                if (item.statu == true) {
                    let obj = item.y + '-' + item.m;
                    arrlist.push(obj);
                }
            });
            let _tempArr = [];
            let tempArr = [];
            let life_room_price = 0;
            let totalPriceAll = 0;
            for (const k in this.options) {
                for (let i = 0; i < arrlist.length; i++) {
                    if (arrlist[i] == k && this.options[k].length > 0) {
                        for (let i = 0; i < this.options[k].length; i++) {
                            this.myAllCheckedStyle == 'circle' ? (this.options[k][i].isChecked = 'circle') : (this.options[k][i].isChecked = 'checked');
                            life_room_price = this.options[k].reduce((life_room_price, item) => {
                                return (life_room_price += item.lrPrice);
                            }, 0);
                            tempArr =
                                this.myAllCheckedStyle == 'circle'
                                    ? ''
                                    : this.options[k].map((obj) => {
                                          return obj.rentalBillSn || obj.lifeCostSn;
                                      });
                        }
                    }
                }
                totalPriceAll += life_room_price;
                _tempArr.push(tempArr);
            }
            this.payForm.totalPrice = this.myAllCheckedStyle == 'circle' ? 0 : totalPriceAll;
            this.payForm.billSn = _tempArr.join(',');
        },
        // 到账日期-1
        onConfirmTime(event) {
            this.timeShow = false;
            this.payForm.payTime = this.formatDate(event);
        },
        // 到账日期-2
        formatDate(date) {
            date = new Date(date);
            let getFullYear = date.getFullYear();
            let getMonth = date.getMonth() + 1 < 10 ? '0' + (date.getMonth() + 1) : date.getMonth() + 1;
            let getDate = date.getDate() < 10 ? '0' + date.getDate() : date.getDate();
            return `${getFullYear}-${getMonth}-${getDate}`;
        },
        // 收款账户-1
        getType() {
            GetBusinessHotelPayeeAccountByhotel({ hotelId: this.$route.query.hotelId }).done((res) => {
                this.userAll = res.data;
                this.users = res.data.map((item) => item.accountName);
                this.payForm.payeeAccountId = this.userAll[0].id;
                this.payForm.payeeAccountIdValue = this.userAll[0].accountName + '>';
            });
        },
        // 收款账户-2
        onConfirm(val) {
            this.payForm.payeeAccountIdValue = val + '>';
            this.payForm.payeeAccountId = this.userAll.find((item) => item.accountName == val).id;
            this.payeeAccountIdValueShow = false;
        },
        // 支付方式-1
        getPaymentType() {
            GetSystemDictDataByType({ dictType: 'business_payment' }).done((res) => {
                this.paymentsAll = res.data.filter((item) => {
                    return item.dictValue >= 5;
                });
                this.payments = res.data
                    .filter((item) => {
                        return item.dictLabel && item.dictValue >= 5;
                    })
                    .map((item) => item.dictLabel);
                this.paymentShow = true;
            });
        },
        // 支付方式-2
        onConfirmPayment(val) {
            this.payForm.paymentValue = val + '>';
            this.payForm.payment = this.paymentsAll.find((item) => item.dictLabel == val).dictValue;
            this.paymentShow = false;
        },
        // 确认到账
        confirmPayed() {
            delete this.payForm.payeeAccountIdValue;
            delete this.payForm.username;
            delete this.payForm.paymentValue;
            GetBusinessAccountUnitPay(this.payForm).done((res) => {
                this.showPayed = false;
                Toast({
                    message: res.msg,
                    duration: 900
                });
                this.getUnpayMonth();
            });
        }
    }
};
</script>

<style scoped>
.agreementScroll {
    color: #000;
    overflow-y: auto;
}
.noPaid-time {
    display: flex;
    justify-content: space-between;
    padding: 0 40px;
    font-size: 1rem;
    line-height: 50px;
}
.time-title {
    font-weight: bold;
}
.time-price {
    padding-left: 4px;
    font-size: 0.7rem;
}
.my-li {
    background: #fff;
    margin-bottom: 8px;
    color: #9a9b9c;
    font-size: 0.7rem;
}
.day-content {
    display: flex;
    justify-content: space-between;
    padding: 8px 0;
    align-items: center;
}
.content-top {
    border-bottom: 1px solid #f4f4f4;
}
.content-left {
    text-align: left;
    margin-left: 10px;
}
.checkValue {
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.bottom-allPrice {
    background: #fff;
    display: flex;
    font-size: 14px;
    align-items: center;
    color: #000;
    position: fixed;
    justify-content: space-around;
    bottom: 0;
    left: 0;
    right: 0;
    padding: 10px 0;
}
.bottom-left {
    width: 22%;
    display: flex;
    align-items: center;
    justify-content: center;
}
.bottom-center {
    width: 40%;
}
.bottom-center label {
    font-weight: bold;
}
.bottom-center label span {
    font-size: 1.2rem;
}
.bottom-right {
    width: 20%;
    background: #ff7d41;
    color: #fff;
    border-radius: 20px;
}
.code_s {
    white-space: nowrap;
    background: #fff;
    color: #000;
    border: 1px solid #989898;
}
.payedColor {
    background: #ff7d41;
    color: #fff;
    margin: 10px auto;
    width: 80%;
    border-radius: 5px;
}
/deep/ .van-button {
    height: 30px;
}
/* 到账 */
/deep/ .van-popup {
    width: 90%;
}
/deep/ .van-field__label {
    text-align: left;
}
/deep/ .van-cell {
    padding: 10px 40px;
    text-align: left;
    align-items: baseline;
}
/deep/ .van-field__control {
    line-height: 28px;
}
.my-van-icon {
    display: flex;
    align-items: center;
}
.my-color-000 {
    color: #000;
}
.m40 {
    margin: 0 40px;
}
.m20 {
    margin: 0 20px;
}
.p10 {
    padding: 8px 0;
}
.mr10 {
    margin: 0 30px 0 20px;
}
</style>



